.media-base-left:before, .media-base-left:after {
  content: " ";
  display: table; }
.media-base-left:after {
  clear: both; }
.media-base-left .img {
  float: left;
  margin-right: 1em;
  border: 1px solid #eceeef; }
.media-base-left .txt {
  display: block;
  overflow: hidden;
  margin: 1em 0; }

.firm-list {
  padding: 25px;
  border: 1px solid #eceeef; }
  .firm-list .media-firmitem {
    padding-top: 0;
    padding-bottom: 2em;
    border-bottom: 1px dashed #eceeef; }
  .firm-list p {
    margin-bottom: 1em; }
  .firm-list .media-title {
    font-size: 1.14286em;
    margin-bottom: 1.2em; }
  .firm-list .media-body .btns {
    margin-top: 2.5em; }
  .firm-list .btns .btn {
    margin-right: .5em;
    padding: 8px 17px;
    border: 1px solid #91b81d; }
    .firm-list .btns .btn.btn2 {
      color: #91b81d;
      background-color: #fff; }
  .firm-list .addr {
    color: #616669; }
    .firm-list .addr i {
      font-size: 1.2em;
      margin-right: .5em;
      color: #8f979d; }
  .firm-list .word-icons {
    overflow: hidden; }
    .firm-list .word-icons i {
      float: left;
      margin-right: .5em; }
  .firm-list .thumbnail {
    width: 11.42857em;
    height: 11.42857em;
    line-height: 11.28571;
    box-sizing: content-box;
    margin-right: 38px;
    border: 5px solid #eceeef;
    overflow: hidden; }
    .firm-list .thumbnail:hover {
      border: 5px solid #eceeef; }
    .firm-list .thumbnail img {
      display: inline-block;
      vertical-align: middle; }
  .firm-list .media-right {
    line-height: 1em;
    padding-top: 36px;
    padding-right: 20px; }
    .firm-list .media-right p {
      text-align: center;
      white-space: nowrap; }
  .firm-list .call-txt i {
    margin-right: .5em;
    color: #8f979d; }
  .firm-list .call-num {
    font-size: 1.8em;
    color: #bd0401;
    font-weight: 600; }

.side-firm-list {
  padding: 5px 18px 0;
  border: 1px solid #eceeef; }
  .side-firm-list h3 {
    margin-bottom: 15px;
    line-height: 44px;
    border-bottom: 1px solid #eceeef;
    font-size: 1.2em; }
  .side-firm-list .media-base-left {
    margin-bottom: 20px; }
  .side-firm-list .img {
    width: 5em;
    height: 5em;
    line-height: 4.64286;
    padding: 3px;
    text-align: center; }
  .side-firm-list img {
    vertical-align: middle; }
  .side-firm-list .txt {
    line-height: 1.5em;
    height: 3em;
    overflow: hidden; }


